<template>
  <h2>当前求和为：{{myResult}}</h2>
</template>

<script>
export default {
    name:'MyResult',
    data(){
        return{
            myResult:0
        }
    },
    mounted(){
    
             this.$bus.$on("sumSelect",(selectNum)=>{
                 console.log(selectNum)
                 this.myResult+=selectNum
        });
          this.$bus.$on("sumSelectWait",(selectNum)=>{
                 console.log(selectNum)
                 setTimeout(()=>{this.myResult+=selectNum},2000)
                 //this.myResult+=selectNum
        })
        
       
    }
}
</script>

