<template>
  <div>
    <div id="root">
      <div class="todo-container">
        <div class="todo-wrap">
          <MyHeader />
          <MyList />
          <MyFooter />
          <button @click="getStudent">获取学生信息</button>
          <button @click="getCar">获取汽车信息</button>
        </div>
      </div>
    </div>

    <div class="container">
      <Search/>
      <Users/>
      
    </div>
    <div style='text-align:center'>
    <MyResult/>
    <MyHandle/>
    </div>
      <div>
    <div class="row">
      <div class="col-xs-offset-2 col-xs-8">
        <div class="page-header"><h2>Vue Router Demo</h2></div>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-2 col-xs-offset-2">
        <div class="list-group">
        <!-- 原始a标签跳转 -->
          <!-- <a class="list-group-item active" href="./about.html">About</a>
          <a class="list-group-item" href="./home.html">Home</a> -->
          <!--  vue借助router-link-->
        <router-link class="list-group-item" active-class="active" to="/about">About</router-link>
          <router-link class="list-group-item" active-class="active" to="/home">Home</router-link> 
        </div>
      </div>
      <div class="col-xs-6">
        <div class="panel">
          <div class="panel-body">
            <router-view></router-view>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import MyHeader from "./components/MyHeader.vue";
import MyList from "./components/MyList.vue";
import MyFooter from "./components/MyFooter.vue";
import Search from "./components/Search.vue";
import Users from "./components/Users.vue";
import MyResult from "./components/MyResult.vue";
import MyHandle from "./components/MyHandle.vue";
import axios from "axios";
export default {
  name: "App",
  components: { MyHeader, MyList, MyFooter,Search,Users,MyResult,MyHandle },
  methods: {
    getStudent() {
      axios.get("http://localhost:8082/api/students").then(
        (response) => {
          console.log(response.data);
        },
        (error) => {
          console.log(error.message);
        }
      );
    },
    getCar() {
      axios.get("http://localhost:8082/foo/cars").then(
        (response) => {
          console.log(response.data);
        },
        (error) => {
          console.log(error.message);
        }
      );
    },
 
  },
};
</script>

