<template>
<div><select name="selectNum" id="1" v-model="addNum">
      <option v-for="key in selectNum" :key=key :value="key">{{key}}</option>
     
  </select>
  <button @click="sumSelect1">+</button>
  <button @click="sumSelect2">等一等+</button>
  </div>
  
</template>

<script>
export default {
    name:'MyHandle',
    data(){
        return{
            addNum:1,
            selectNum:[1,2,3]


        }
    },
    methods:{
        sumSelect1(){
            this.$bus.$emit("sumSelect",this.addNum)
        },
        sumSelect2(){
            this.$bus.$emit("sumSelectWait",this.addNum)
        }
    }
}
</script>

